.register-container {
    display: flex;
    justify-content: center;
    padding: 50px 0px;
}

.register-container-blog {
    width: 500px;
    border-radius: 50px;
    padding: 25px 50px;
    box-sizing: border-box;

    background: rgba(255, 255, 255, 0.8);
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.5);
}

.register-top {
    display: flex;
    justify-content: center;
    align-items: center;
}

.register-top h1 {
    color: var(--blue-color);
}

.register-form {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
}

.register-form-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
    padding: 2px 0px;
}

.register-form-left {
    width: auto;
}

.register-form-right {
    width: 60%;
}

.register-form-right input {
    width: 100%;
    border-width: 0px;
    border-radius: 25px;
    box-sizing: border-box;
    padding: 8px;
} 

.register-form-right select {
    width: 100%;
    border-width: 0px;
    border-radius: 25px;
    box-sizing: border-box;
    padding: 8px;
} 

.error-message {
    text-align: left;
    color: red;
}

.success-message {
    text-align: left;
    color: green;
}

.register-btn {
    height: 32px;
    width: 50%;
    margin-top: 16px;
}


@media screen and (max-width: 500px) {
    .register-container {
        display: flex;
        justify-content: center;
        padding: 20px 0px;
    }
    
    .register-container-blog {
        width: 90%;
        min-width: 300px;
        padding: 25px 30px;
    }

    .register-form-right {
        width: 50%;
    }
}